@import url('../index.css');
*{
  box-sizing: border-box;
}

.LinksNavPar {
  display: none;
}

.main-nav {
  position: absolute;
  background: #fff;
  z-index: 99;
  width: 100%;
  height: 100px;
  align-items: center;
  top: 0;
  padding: 2% 10%;
  justify-content: space-between;
  display: flex;
}
nav ul{
  display: flex;
  flex-direction: row;
  padding: 0;
  margin: 0;
  list-style: none;
  gap:1em;

}
.burgerNavContainer {
  width: 100%;
  height: 25vh;
  background-color: var(--colorPrimery);
  position: absolute;
  top: 7.3em;
  right: 0;
  display: flex;
  flex-direction: column;
  gap: 18px;
  justify-content: center;
  align-items: center;
  transition: all 0.5s ease-out;
}

.burgerNavContainer {
  display: none;
}

.LinkNav {
  text-decoration: none;
  color: var(--blackCol);
  font-size: var(--MobileH2);
  font-weight: normal;
  width: 85px;
  transition: font-weight 0.2;
  text-align: center;
  z-index: 99;
}

.burgerContainer {
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  flex-direction: column;
  gap: 8px;
  z-index: 99;
}

.burger-line {
  width: 40px;
  height: 3.33px;
  background-color: var(--blackCol);
  transition: ease-in 0.4s;
}

.shortLine {
  width: 25px;
}

.leftFooter {
  display: flex;
  flex-direction: column;

  color: var(--whiteCol);
  width: 40%;
}

.leftFooter img {
  width: 129px;

}

.rightFooter {

  width: 30%;
  margin: 0;
}

.footer {
  background-color: var(--blackCol);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100vh;

}

.leftFooter {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  width: 90%;
  height: 300px;
  gap: 16px;
  position: relative;
  left: 5%;
  top: 10%;

}

.leftFooter img {
  position: relative;
  right: 6%;
}

.leftFooter p {
  padding: 0;
  margin: 0;
  font-weight: 600;
}

.rightFooter {
  width: 90%;
  height: 450px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  color: var(--whiteCol);
  position: relative;
  margin: auto;
  left: 0%;
}

.rightFooter h3 {
  font-size: var(--MobileHeading);
  color: var(--whiteCol);


}

.rightFooter p {
 
  color: var(--whiteCol);
  width: 100%;
}

.subscribeBox {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 1%;
  width: 100%;
  justify-content: space-between;
}

.subscribeBox p {
  width: 100%;
  margin-bottom: 0;
}

.subscribeBox input {
  height: 50px;
  width: 60%;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  padding: 0px;
  gap: 12px;
}


.logo {
  width: 80px;
  height: 80px;
  z-index: 56;
}

.anime-line {
  height: 2em;
  transition: height 2s ease-in;
  background-color: transparent;
}

.anime-line {
  animation-name: fillLine;
  animation-duration: 2s;
  animation-timing-function: ease-in;
}

.VisService {
  opacity: 0;
}

.fade-in {
  opacity: 1;
  transition: opacity 2s ease-in;
}


@keyframes fillLine {
  0% {
    height: 0;
  }

  100% {
    height: 2em;
  }
}


@media (min-width: 768px) {

  .burgerNavContainer {
    display: none;
  }

  .burgerContainer {
    display: none;
  }

  .main-nav {
    position: absolute;
    background: transparent;
    height: 10em;
  }

  .LinkNav {
    font-size: var(--btnSize);
  }

  .logo {
    width: 109px;
    height: 109px;
    z-index: 56;
  }

  .LinksNav {
    align-items: right;
    gap: 40px;
  }

  .LinksNav a {
    text-decoration: none;
  }

  .navBarWhite {
    color: #fff;
  }

  .navBarBlack {
    color: #000;
  }

  .LinkNav:hover {
    font-weight: bold;
  }

  .footer {
    flex-direction: row;
    height: 30em;
    gap: 5%;
    z-index: 30;
   
  }

  .leftFooter {
    width: 40%;
    right: 0%;
    justify-content: center;
    top: 0;
    left: 0;
    margin: auto;
    

  }
  .leftFooter img:nth-child(1) {
    position: relative;
    right: 3%;
  }
  .rightFooter {
    width: 40%;
    left: 0%;
    height: 300px;
    
  }
  .footer-btn{
    width: 35%;
  }
  .subscribeBox{
    width: 100%;
  }
  .subscribeBox input {
    width: 60%;
  }
  .rightFooter h3 {
    font-size: var(--headingSize);
  }

  .rightFooter p {
    font-size: var(--paragraph);
  }


}